no-repeat padding-box;\n    border-radius: 10px;\n}\n.modules-datatable[data-v-268badb3]::-webkit-scrollbar-thumb:hover {\n    background: #555;\n}\n.row-container[data-v-268badb3] {\n    width: 100%;\n    height: 36px;\n    position: relative;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #e0e0e0;\n}\n.chevron[data-v-268badb3] {\n    position: absolute;\n    left: 4px;\n    color: #bcbcbc;\n    font-size: 24px;\n}\n.module-checkbox[data-v-268badb3] {\n    position: absolute;\n    top: 10px;\n    left: 43px;\n    cursor: pointer;\n}\n.module-name[data-v-268badb3] {\n    position: absolute;\n    top: 9px;\n    left: 69px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n.model-checkbox[data-v-268badb3] {\n    position: absolute;\n    top: 10px;\n    left: 59px;\n    cursor: pointer;\n}\n.model-icon[data-v-268badb3] {\n    position: absolute;\n    width: 16px;\n    height: 16px;\n    top: 6px;\n    left: 85px;\n}\n.app-tier-model-icon[data-v-268badb3] {\n    position: absolute;\n    width: 16px;\n    height: 16px;\n    top: 6px;\n    left: 69px;\n}\n.app-tier-model-name[data-v-268badb3] {\n    position: absolute;\n    top: 10px;\n    left: 87px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n.model-name[data-v-268badb3] {\n    position: absolute;\n    top: 9px;\n    left: 103px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n.version-text[data-v-268badb3] {\n    margin: 10px 7px 10px auto;\n    display: table;\n    font: normal normal normal 12px/16px Roboto;\n    color: #757575;\n    cursor: pointer;\n    user-select: none;\n}\n.version-icon[data-v-268badb3] {\n    font: normal normal normal 16px/19px Material Design Icons;\n    color: #151233;\n}\n.footer[data-v-268badb3] {\n    position: relative;\n    top: 533px;\n    width: 100%;\n}\n.cancel-button[data-v-268badb3] {\n    position: absolute;\n    top: 16px;\n    left: 609px;\n    width: 86px;\n    height: 40px;\n    border-radius: 4px;\n    user-select: none;\n    font-style: normal;\n    font-weight: 500;\n    font-family: \"ROBOTO\";\n    font-size: 15px;\n    color: #757575;\n    padding: 10px 20px 10px 20px;\n    cursor: pointer;\n}\n.save-button[data-v-268badb3] {\n    position: absolute;\n    top: 16px;\n    left: 703px;\n    width: 77px;\n    height: 40px;\n    background: #151233 0% 0% no-repeat padding-box;\n    border-radius: 4px;\n    user-select: none;\n    font-weight: 500;\n    font-family: \"ROBOTO\";\n    font-size: 15px;\n    color: #ffffff;\n    padding: 10px 20px 10px 20px;\n    cursor: pointer;\n}\n.button-disable[data-v-268badb3] {\n    opacity: 0.5;\n    cursor: default;\n    pointer-events: none;\n}\n.qjson-color[data-v-268badb3] {\n    color: #449dd1;\n}\n.yaml-color[data-v-268badb3] {\n    color: #449dd1;\n}\n.js-color[data-v-268badb3] {\n    color: #449dd1;\n}\n.process-color[data-v-268badb3] {\n    color: #007c77;\n}\n.bpmn-color[data-v-268badb3] {\n    color: #007c77;\n}\n.entity-designer-color[data-v-268badb3] {\n    color: #e9724c;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/preview/customPreviewSettings.vue"],"names":[],"mappings":";AA8TA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;AACb;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,2CAA2C;IAC3C,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,2CAA2C;IAC3C,cAAc;IACd,iBAAiB;IACjB,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,SAAS;IACT,UAAU;IACV,+CAA+C;IAC/C,yBAAyB;IACzB,kBAAkB;IAClB,2CAA2C;IAC3C,mBAAmB;IACnB,2BAA2B;AAC/B;AAEA;IACI,qBAAqB;AACzB;AAEA;IACI,YAAY;AAChB;AAEA;IACI,cAAc;IACd,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,eAAe;AACnB;AACA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,2CAA2C;IAC3C,cAAc;AAClB;AAEA;IACI,yCAAyC;IACzC,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,YAAY;IACZ,aAAa;IACb,yBAAyB;IACzB,kBAAkB;IAClB,gBAAgB;AACpB;AACA;IACI,UAAU;AACd;AAEA;IACI,+CAA+C;IAC/C,mBAAmB;AACvB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,+CAA+C;IAC/C,yBAAyB;AAC7B;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,cAAc;IACd,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,UAAU;IACV,2CAA2C;IAC3C,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,QAAQ;IACR,UAAU;AACd;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,QAAQ;IACR,UAAU;AACd;AACA;IACI,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,2CAA2C;IAC3C,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,WAAW;IACX,2CAA2C;IAC3C,cAAc;AAClB;AAEA;IACI,0BAA0B;IAC1B,cAAc;IACd,2CAA2C;IAC3C,cAAc;IACd,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,0DAA0D;IAC1D,cAAc;AAClB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,WAAW;AACf;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,qBAAqB;IACrB,eAAe;IACf,cAAc;IACd,4BAA4B;IAC5B,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,WAAW;IACX,YAAY;IACZ,+CAA+C;IAC/C,kBAAkB;IAClB,iBAAiB;IACjB,gBAAgB;IAChB,qBAAqB;IACrB,eAAe;IACf,cAAc;IACd,4BAA4B;IAC5B,eAAe;AACnB;AAEA;IACI,YAAY;IACZ,eAAe;IACf,oBAAoB;AACxB;AAEA;IACI,cAAc;AAClB;AAEA;IACI,cAAc;AAClB;AAEA;IACI,cAAc;AAClB;AAEA;IACI,cAAc;AAClB;AAEA;IACI,cAAc;AAClB;AAEA;IACI,cAAc;AAClB","sourcesContent":["<template>\n    <div class=\"settings-container\">\n        <span class=\"own-domain-text\"> I have my own domain to use. </span>\n        <RadioButtonComp class=\"radio-button\" style=\"left: 20px\" :active=\"useOwnedDomain\" @click=\"handleOwnDomain(true)\" />\n        <span class=\"radio-text\" style=\"left: 44px\" @click=\"handleOwnDomain(true)\">Yes</span>\n        <RadioButtonComp class=\"radio-button\" style=\"left: 85px\" :active=\"!useOwnedDomain\" @click=\"handleOwnDomain(false)\" />\n        <span class=\"radio-text\" style=\"left: 109px\" @click=\"handleOwnDomain(false)\">No</span>\n\n        <input\n            type=\"text\"\n            :class=\"'existing-app-url' + (useOwnedDomain ? existingAppURL.length == 0 ? ' url-error' : '' : ' url-disable')\"\n            placeholder=\"Existing App URL\"\n            :disable=\"!useOwnedDomain\"\n            :readonly=\"!useOwnedDomain\"\n            v-model=\"existingAppURL\"\n            @input=\"handleExistingAppURL\"\n        />\n\n        <CheckboxComp\n            class=\"modules-selected-checkbox\"\n            :minus=\"checkedModuleCount != totalModuleCount && checkedModuleCount != 0\"\n            :check=\"checkedModuleCount == totalModuleCount\"\n            @click=\"handleModuleCountCheckboxEvent(checkedModuleCount != totalModuleCount && checkedModuleCount == 0)\"\n        />\n        <div class=\"modules-selected-text\">\n            <span class=\"text-number\">{{ checkedModuleCount }}</span> of <span class=\"text-number\">{{ totalModuleCount }}</span> modules selected\n        </div>\n\n        <div class=\"modules-datatable\">\n            <div v-for=\"item in items\">\n                <div v-if=\"item?.type == 'module'\">\n                    <div class=\"row-container\">\n                        <i :class=\"'mdi mdi-menu' + (item.collapsed ? '-right' : '-down') + ' chevron'\" @click=\"handleCollapse(item)\" />\n                        <CheckboxComp\n                            class=\"module-checkbox\"\n                            :check=\"item.allChildChecked\"\n                            :minus=\"item.anyChildChecked && !item.allChildChecked\"\n                            @click=\"handleItemCheckEvent(item)\"\n                        />\n                        <span class=\"module-name\">{{ item.name }}</span>\n                    </div>\n                    <div class=\"row-container\" v-show=\"!item.collapsed\" v-for=\"child in item?.children\">\n                        <CheckboxComp class=\"model-checkbox\" :check=\"child.checked\" @click=\"handleItemCheckEvent(child)\" />\n                        <i v-if=\"child.type == 'qjson'\" class=\"mdi mdi-palette qjson-color model-icon\" />\n                        <i v-if=\"child.type == 'yaml'\" class=\"mdi mdi-cog-outline yaml-color model-icon\" />\n                        <i v-if=\"child.type == 'js' || child?.type == 'css'\" class=\"mdi mdi-cog-outline js-color model-icon\" />\n                        <i v-if=\"child.type == 'process'\" class=\"mdi mdi-auto-fix process-color model-icon\" />\n                        <i v-if=\"child.type == 'bpmn'\" class=\"mdi mdi-vector-polyline bpmn-color model-icon\" />\n                        <i v-if=\"child.type == 'entityDesigner'\" class=\"mdi mdi-database entity-designer-color model-icon\" />\n                        <i v-if=\"child.type == 'flow'\" class=\"mdi mdi-step-forward bpmn-color model-icon\" />\n                        <span class=\"model-name\">{{ child.name }}</span>\n                        <div class=\"version-text\" @click=\"versionDialog(child)\">\n                            {{ child.version }} <i class=\"mdi mdi-pencil-outline version-icon\"></i>\n                        </div>\n                    </div>\n                </div>\n                <div v-else>\n                    <div class=\"row-container\">\n                        <CheckboxComp class=\"module-checkbox\" :check=\"item?.checked\" @click=\"handleItemCheckEvent(item)\" />\n                        <i v-if=\"item?.type == 'qjson'\" class=\"mdi mdi-palette qjson-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'yaml'\" class=\"mdi mdi-cog-outline yaml-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'js' || item?.type == 'css'\" class=\"mdi mdi-cog-outline js-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'process'\" class=\"mdi mdi-auto-fix process-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'bpmn'\" class=\"mdi mdi-vector-polyline bpmn-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'entityDesigner'\" class=\"mdi mdi-database entity-designer-color app-tier-model-icon\" />\n                        <i v-if=\"item?.type == 'flow'\" class=\"mdi mdi-step-forward bpmn-color app-tier-model-icon\" />\n                        <span class=\"app-tier-model-name\">{{ item?.name }}</span>\n                        <span class=\"version-text\" @click=\"versionDialog(item)\">\n                            {{ item?.version }} <i class=\"mdi mdi-pencil-outline version-icon\"></i>\n                        </span>\n                    </div>\n                </div>\n            </div>\n        </div>\n\n        <div class=\"footer\">\n            <div class=\"cancel-button\" @click=\"close\">Cancel</div>\n            <div :class=\"'save-button' + (saveButtonDisable ? ' button-disable' : '')\" @click=\"save\">SAVE</div>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IModel, IModule } from \"../../../../../domain/model/models\";\nimport { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\nimport CheckboxComp from \"../../checkboxComp.vue\";\nimport RadioButtonComp from \"../../radioButtonComp.vue\";\nimport { ref, computed, defineAsyncComponent, onMounted } from \"vue\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport { ILivePreviewUseCase } from \"../../../../../domain/useCase/ILivePreviewUseCase\";\nimport { ICustomLivePreview, ILivePreviewModel } from \"@stechquick/algae/lib/qCloudTemp/livePreview\";\nimport { IDialog } from \"../../../../../domain/presentation/IDialog\";\nimport { IQJsonAdditionals } from \"@stechquick/algae/lib/qCloudTemp/quickCloud\";\n\ninterface ITableData {\n    id: string;\n    parentId?: string;\n    collapsed?: boolean;\n    anyChildChecked?: boolean;\n    allChildChecked?: boolean;\n    checked?: boolean;\n    name: string;\n    type: IModel[\"modelType\"] | IModule[\"objectType\"];\n    version?: string;\n    children?: Array<ITableData>;\n    usageType?: string;\n    isRootPage?: boolean;\n}\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst livePreviwUseCase = container.resolve<ILivePreviewUseCase>(ILivePreviewUseCase);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst emit = defineEmits([\"close\"]);\nconst customPreviewID = ref<string>(\"\");\nvar update = false;\n\n//let selectedModelIDs = Array<string>();\nconst items = ref<Array<ITableData>>([]);\nconst useOwnedDomain = ref<boolean>(false);\nconst existingAppURL = ref<string>(\"\");\nlet livePreviewInfo:ICustomLivePreview | undefined = undefined;\n\nonMounted(async () => \n{\n    livePreviewInfo =  await livePreviwUseCase.getCustomLivePreviewInfo(viewModel.studio.appId);\n    if(livePreviewInfo){\n        update = true;\n        customPreviewID.value = livePreviewInfo.ID;\n        executor.execute(\n            async () => {\n                const links = livePreviewInfo!.links;\n                if (links?.length != 0) {\n                    const link = links![0];\n                    useOwnedDomain.value = link.isActive;\n                    existingAppURL.value = link.url;\n                }\n                handleItems();\n            },\n            { loading: true }\n        );\n    }\n    else {\n        handleItems();\n    }\n});\n\nconst totalModuleCount = computed(() => items.value.filter((item) => item.type == \"module\").length);\nconst checkedModuleCount = computed(() => items.value.filter((item) => item.type == \"module\" && item.allChildChecked).length);\nconst saveButtonDisable = computed(\n    () => !items.value.some((item) => item.checked || item.anyChildChecked) || (useOwnedDomain.value && existingAppURL.value.length == 0)\n);\n\nfunction handleItems() {\n    items.value = viewModel.studio.items\n        .filter((item) => item.objectType == \"module\" &&  item.children.length > 0)\n        .map(\n            (item) =>\n                {\n                    const children = (item as IModule).children.filter((child) => child.objectType == \"model\" && child.modelType == \"qjson\");\n                    return {\n                    id: item.ID,\n                    collapsed: true,\n                    name: item.name,\n                    type: \"module\",\n                    anyChildChecked: update ? children.some(child => livePreviewInfo?.models.some(model => model.ID == child.ID)) : false,\n                    allChildChecked: update ? children.every(child => livePreviewInfo?.models.some(model => model.ID == child.ID)) : false,\n                    children: children\n                        .map(\n                            (child) =>\n                                {\n                                    return {\n                                    id: child.ID,\n                                    parentId: item.ID,\n                                    checked: update ? livePreviewInfo?.models.some(model => model.ID == child.ID) : false,\n                                    name: child.name,\n                                    type: (child as IModel).modelType,\n                                    version: livePreviewInfo?.models.find(model => model.ID == child.ID)?.ver || \"latest\",\n                                } as ITableData}\n                        ),\n                } as ITableData}\n        );\n    viewModel.studio.items\n        .filter((item) => item.objectType == \"model\" && item.usageType != \"appSettings\" && item.modelType == \"qjson\")\n        .forEach((itemModel) => {\n            const item = itemModel as IModel;\n            const isRootPage = item.modelType == \"qjson\" && (item.additionals as IQJsonAdditionals).isRootPage ? true : undefined;\n            items.value.push({\n                id: item.ID,\n                checked: update ? livePreviewInfo?.models.some(model => model.ID == item.ID) : false,\n                name: item.name,\n                type: item.modelType,\n                version: livePreviewInfo?.models.find(model => model.ID == item.ID)?.ver || \"latest\",\n                isRootPage,\n            });\n        });\n    const uiSettingsID = CryptoHelper.CreateGuid();\n    const appSettings = viewModel.studio.items.filter((item) => item.objectType == \"model\" && item.usageType == \"appSettings\") as Array<IModel>;\n    const uiSettings: ITableData = {\n        id: uiSettingsID,\n        collapsed: true,\n        name: \"UI Settings\",\n        type: \"module\",\n        anyChildChecked: update ? appSettings.some((setting) => livePreviewInfo?.models.some(model => model.ID == setting.ID)) : false,\n        allChildChecked: update ? appSettings.every((setting) => livePreviewInfo?.models.some(model => model.ID == setting.ID)) : false,\n        children: appSettings.map((setting) => ({\n            id: setting.ID,\n            parentId: uiSettingsID,\n            name: setting.name,\n            type: (setting as IModel).modelType,\n            checked: update ? livePreviewInfo?.models.some(model => model.ID == setting.ID) : false,\n            usageType: setting?.usageType,\n            version: livePreviewInfo?.models.find(model => model.ID == setting.ID)?.ver || \"latest\",\n        })) as Array<ITableData>,\n    };\n    items.value.unshift(uiSettings);\n}\n\nfunction shiftModelsToEndOfList(first: ITableData, second: ITableData) {\n    if (first.type == \"module\" && second.type != \"module\") return 1;\n    else if (first.type != \"module\" && second.type == \"module\") return -1;\n    else return 0;\n}\n\nfunction handleOwnDomain(value: boolean) {\n    useOwnedDomain.value = value;\n}\n\nfunction handleExistingAppURL($event: any) {\n    existingAppURL.value = $event.target.value;\n}\n\nfunction handleModuleCountCheckboxEvent(value: boolean) {\n    items.value.forEach((item) => {\n        item.checked = value;\n        if (item.allChildChecked != undefined) item.allChildChecked = value;\n        if (item.anyChildChecked != undefined) item.anyChildChecked = value;\n        if (item.children) item.children.forEach((child) => (child.checked = value));\n    });\n}\n\nfunction handleCollapse(item: ITableData) {\n    item.collapsed = !item.collapsed;\n}\n\nfunction handleItemCheckEvent(item: ITableData) {\n    if (item.type != \"module\") {\n        item.checked = !item.checked;\n        if (item.parentId) {\n            const parent = items.value.find((i) => i.id == item.parentId)!;\n            handleParentCheckedStatus(parent);\n        }\n    } else {\n        if (item.anyChildChecked) {\n            item.children?.forEach((child) => (child.checked = false));\n            item.checked = false;\n        } else {\n            item.children?.forEach((child) => (child.checked = true));\n            item.checked = true;\n        }\n        handleParentCheckedStatus(item);\n    }\n}\n\nfunction handleParentCheckedStatus(item: ITableData) {\n    item.allChildChecked = !item.children?.some((child) => !child.checked);\n    item.anyChildChecked = item.children?.some((child) => child.checked);\n}\n\nfunction versionDialog(item: ITableData) {\n    const clickedNode = { id: item.id, state: { checked: item.checked }, text: item.name, type: item.type, version: item.version };\n    \n    const closeCb = async (reason: any) => {\n        item.version = clickedNode.version;\n    }\n\n    dialog.showDialog(\n        defineAsyncComponent(() => import(\"../../application/modelVersionDialog.vue\")),\n        { closable: true, title: \"Change Version\", width: \"900px\", maxHeight: \"500px\", overflow: \"auto\", closeCb },\n        { clickedNode }\n    );\n}\n\nfunction close() {\n    emit(\"close\");\n}\n\nfunction save() {\n    const models: ICustomLivePreview[\"models\"] = items.value\n        .flatMap((item) => (item.type == \"module\" ? item.children : item))\n        .filter((item) => item?.checked)\n        .map((item) => ({ ID: item?.id, ver: item?.version, usageType: item?.usageType,isRootPage: item?.isRootPage } as ILivePreviewModel));\n    const links: ICustomLivePreview[\"links\"] = [];\n\n    if(existingAppURL.value.length != 0){\n        links.push({\n            type: \"custom\",\n            url: existingAppURL.value,\n            isActive: useOwnedDomain.value,\n        });\n    }\n    executor.execute(\n        async () => {\n            if (!update) {\n                const createdLivePreview = await livePreviwUseCase.createLivePreview({ appID: viewModel.studio.appId, type: \"custom\", links, models, selected: true });\n            } else {\n                await livePreviwUseCase.updateLivePreview(customPreviewID.value, { links, models });\n            }\n            emit(\"close\");\n        },\n        { loading: true }\n    );\n}\n</script>\n\n<style scoped>\n.settings-container {\n    position: relative;\n    top: 0px;\n    left: 0px;\n}\n\n.own-domain-text {\n    position: absolute;\n    top: 28px;\n    left: 20px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.radio-button {\n    position: absolute;\n    top: 60px;\n    left: 20px;\n    cursor: pointer;\n}\n\n.radio-text {\n    position: absolute;\n    top: 59px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n    user-select: none;\n    cursor: pointer;\n}\n\n.existing-app-url {\n    position: absolute;\n    width: 760px;\n    height: 40px;\n    top: 94px;\n    left: 20px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #bcbcbc;\n    border-radius: 4px;\n    font: normal normal normal 14px/19px Roboto;\n    letter-spacing: 0px;\n    padding: 10px 0px 10px 10px;\n}\n\n.url-error {\n    border: 1px solid red;\n}\n\n.url-disable {\n    opacity: 0.5;\n}\n\n.existing-app-url::placeholder {\n    color: #bcbcbc;\n    user-select: none;\n}\n\n.modules-selected-checkbox {\n    position: abso